@import url(https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,400;0,700;0,900;1,400;1,700;1,900&family=Roboto:ital,wght@0,400;0,700;1,400&display=swap);
html{box-sizing:border-box}*,*:before,*:after{box-sizing:inherit}body{margin:0}h1,h2,h3,p{margin-top:0}img{max-width:100%;height:auto}body{font-family:"Roboto",sans-serif}h1,h2,h3,nav a{font-family:"Lato",sans-serif}h1{font-weight:800;font-style:italic}a{color:#000}body{background-color:#575454;color:#fff}.wrapper{max-width:62.5em;margin:0 auto;border-radius:10px;background-color:rgba(128,128,128,.2)}@media(min-width: 62.5em){.wrapper{margin:0.5em auto;border:3px solid #fff}}header h1{margin:0.125em 0 0.375em;text-align:center}header a{text-decoration:none}@media(min-width: 33.75em){header{display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between;-webkit-align-items:center;align-items:center;margin:0.75em}header h1{margin:0;text-align:left}}@media(min-width: 62.5em){header{margin:1.25em}header h1{font-size:3em}}nav ul{margin:0;padding:0;list-style:none;display:-webkit-flex;display:flex}nav li{-webkit-flex:1 1;flex:1 1;margin-right:0.25em}nav li:last-child{margin-right:0}nav a{display:block;text-align:center;line-height:2.5em;text-decoration:none}nav a:hover,nav a:focus,nav a:active{background-color:rgba(255,255,255,.3);border-radius:10px}.nav-main li{background-color:#0362fc;border:2px,bold,#0362fc;border-radius:10px}.nav-main a{font-weight:bold}.nav-main a.active{background-color:#024eca;border:2px,bold,#0362fc;border-radius:10px}@media(min-width: 33.75em){.nav-main a{padding:0 1.25em}}.nav-sort{margin:auto 1.25em}.nav-sort li{background-color:#e22d15;border:2px,bold,#e22d15;border-radius:10px;margin:auto 5px}.nav-sort a{font-size:1em;font-weight:bold}.nav-sort a.active{background-color:#bd2612;border:2px,bold,#e22d15;border-radius:10px}.movies-container{padding:0.75em}@media(min-width: 33.75em){.movies-container{display:grid;grid-template-columns:1fr 1fr;grid-gap:0.875em;gap:0.875em}}@media(min-width: 51.25em){.movies-container{padding:1.25em;grid-template-columns:1fr 1fr 1fr;grid-gap:1.125em;gap:1.125em}}.movie-card{background-color:rgba(0,0,0,.3);border:0.1875em solid #cfc5c5;padding:0.625em;margin-bottom:1em;border-radius:0.5em}.movie-card img{width:100%;border-radius:0.25em}.movie-card h3{text-align:center}.movie-card a{color:#fff}@media(min-width: 33.75em){.movie-card{margin-bottom:0}}.movie-poster{padding:40px 0;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center}.movie-poster img{text-align:center;border:5px solid #fff;border-radius:10px}.about-page,.favs-page{padding:1.25em}footer{border-top:2px dotted rgba(255,255,255,.6);padding:0.625em}footer p{margin:0}
